  props: ["reason"],
  data() { 
	  return {
		  chooseData: null
	  }
  },
  methods: {
	  choose(data){
			this.chooseData = data;  
	  },
	  cancel_confirm() {
			wx.showModal({
			title: '',
			content: '您确定要取消该订单吗？',
			success: function (sm) {
				if (sm.confirm) {
					// 用户点击了确定 可以调用删除方法了
					} else if (sm.cancel) {
					return;
					}
				}
			})
	  },
		getCancelReason() {
			
		}
  }
}



