













	import orderItem from '../../components/orderItem.vue'
	import ut from '../../utils/index.js';
	export default {
		components: {
            orderItem
        },
		data() {
			return {
				order_list: [],
				cancel_reason: []
			}
		},
		onLoad() {
			this.getOrderList();
			this.getReasonType();
		},
		methods: {
			getOrderList() {
				ut.request({
					data: {
						type: 1
					},
					method: 'get',
					url: "service/order/list"
				}).then(data=>{
					console.log(data)
					this.order_list = data;
				})
			},
			getReasonType() {
				ut.request({
					data: {
						type: 1
					},
					method: 'get',
					url: "service/order/cancelReason"
				}).then(data=>{
					console.log(data)
					this.cancel_reason = data;
				})
			}
		}
	}



